import { useEffect, useState } from 'react';
import { getBlogsApi, removeBlogApi } from '../apis/blogApi';
import AuthNotify from '../components/AuthNotify';
import BlogItem from '../components/BlogItem';
import { useAppSelector } from '../hooks';
import { authCurrentUser } from '../redux/auth/authSelectors';
import { Blog } from '../types';

const Dashboard: React.FC = () => {
    const currentUser = useAppSelector(authCurrentUser);
    const [blogs, setBlogs] = useState<Blog[]>(currentUser?.blogs || []);

    const fetchBlogs = async () => {
        const response = await getBlogsApi();

        if (response.data?.blogs) setBlogs(response.data.blogs);
    };

    useEffect(() => {
        fetchBlogs();
    }, []);

    const removeBlogHandler = async (id: string) => {
        await removeBlogApi(id);

        fetchBlogs();
    };

    return (
        <>
            {!currentUser && <AuthNotify />}
            {currentUser && (
                <div className="d-flex flex-column gap-4">
                    {!blogs.length && (
                        <p className="text-center fs-5 text-dark">
                            You don't have any blogs 😢😢😢
                        </p>
                    )}
                    {blogs.map(blog => (
                        <BlogItem
                            key={blog.id}
                            blog={blog}
                            onRemoveBlog={removeBlogHandler}
                        />
                    ))}
                </div>
            )}
        </>
    );
};

export default Dashboard;
