import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
    createBlogApi,
    getPreSignedBlogImgApi,
    uploadBlogImgApi,
} from '../apis/blogApi';
import AuthNotify from '../components/AuthNotify';
import { useAppSelector } from '../hooks';
import { authCurrentUser } from '../redux/auth/authSelectors';

const CreateBlog: React.FC = () => {
    const currentUser = useAppSelector(authCurrentUser);
    const [validated, setValidated] = useState(false);
    const [title, setTitle] = useState<string>('');
    const [content, setContent] = useState<string>('');
    const [image, setImage] = useState<File>();
    const navigate = useNavigate();

    const createBlogHandler = async (ev: React.FormEvent<HTMLFormElement>) => {
        ev.preventDefault();

        setValidated(true);

        if (ev.currentTarget.checkValidity() && image) {
            const responseUrl = await getPreSignedBlogImgApi(image.type);

            if (!responseUrl.data) {
                toast.error('Failed to upload');
                return;
            }

            const { key, url } = responseUrl.data;

            await uploadBlogImgApi(url, image);

            const urlObj = new URL(url);
            const imageUrl = `${urlObj.protocol}//${urlObj.hostname}/${key}`;

            const response = await createBlogApi({
                title,
                content,
                image: imageUrl,
            });

            if (response.data?.blog) {
                toast.success('Create blog successfullt');

                navigate('/', { replace: true });
            }
        }
    };

    const chooseImageHandler = async (
        ev: React.ChangeEvent<HTMLInputElement>
    ) => {
        const file = ev.target.files?.[0];

        file && setImage(file);
    };

    return (
        <>
            {!currentUser && <AuthNotify />}
            {currentUser && (
                <Form
                    noValidate
                    validated={validated}
                    onSubmit={createBlogHandler}
                >
                    <Form.Group className="my-3">
                        <Form.Label>Title</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="Title"
                            value={title}
                            onChange={e => setTitle(e.target.value)}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Please provide title.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="my-3">
                        <Form.Label>Content</Form.Label>
                        <Form.Control
                            as="textarea"
                            rows={5}
                            placeholder="Content"
                            value={content}
                            onChange={e => setContent(e.target.value)}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Please provide content.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="position-relative mb-3">
                        <Form.Label>Image</Form.Label>
                        <Form.Control
                            type="file"
                            onChange={chooseImageHandler}
                            accept="image/*"
                            required
                        />

                        <Form.Control.Feedback type="invalid">
                            Please provide blog image.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Button className="mt-3" type="submit">
                        Create blog
                    </Button>
                </Form>
            )}
        </>
    );
};

export default CreateBlog;
