import { Container, Navbar, NavDropdown } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { logOutApi } from '../apis/userApi';
import { useAppSelector } from '../hooks';
import { authCurrentUser } from '../redux/auth/authSelectors';
import { handleLoginGoogle } from '../utils';

const Header: React.FC = () => {
    const currentUser = useAppSelector(authCurrentUser);
    const navigate = useNavigate();

    const loginHandler = () =>
        handleLoginGoogle(loginSucces =>
            loginSucces
                ? window.location.reload()
                : toast.error('Failed to login')
        );

    const logoutHandler = async () => {
        await logOutApi();

        window.location.reload();
    };

    return (
        <Navbar bg="dark" variant="dark py-3">
            <Container>
                <Link to="/" className="text-decoration-none">
                    <Navbar.Brand>Blogs</Navbar.Brand>
                </Link>
                <Navbar.Toggle />
                <Navbar.Collapse className="justify-content-end">
                    {!currentUser && (
                        <Navbar.Text
                            onClick={loginHandler}
                            className="btn btn-link text-light text-decoration-none py-0"
                        >
                            Login
                        </Navbar.Text>
                    )}
                    {currentUser && (
                        <NavDropdown
                            title={currentUser.name}
                            id="basic-nav-dropdown"
                            className="text-light py-1"
                        >
                            <NavDropdown.Item
                                onClick={() => navigate('/blogs/create')}
                            >
                                Create blog
                            </NavDropdown.Item>
                            <NavDropdown.Item onClick={logoutHandler}>
                                Logout
                            </NavDropdown.Item>
                        </NavDropdown>
                    )}
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default Header;
