import { ReactNode, useEffect } from 'react';
import { getMeApi } from '../apis/userApi';
import { useAppDispatch, useAppSelector } from '../hooks';
import { loginUser, logoutUser, setAuthReady } from '../redux/auth/authActions';
import { authIsAuthReady } from '../redux/auth/authSelectors';
import Header from './Header';

interface AppLayoutProps {
    children: ReactNode;
}

const AppLayout: React.FC<AppLayoutProps> = ({ children }) => {
    const dispatch = useAppDispatch();
    const isAuthReady = useAppSelector(authIsAuthReady);

    useEffect(() => {
        (async () => {
            try {
                const response = await getMeApi();

                if (!response.data) throw new Error();

                dispatch(loginUser(response.data.user));
            } catch (error) {
                dispatch(logoutUser());
            } finally {
                dispatch(setAuthReady());
            }
        })();
    }, [dispatch]);

    return (
        <>
            {isAuthReady && (
                <>
                    <Header />
                    <main className="container">
                        <div className="m-5">{children}</div>
                    </main>
                </>
            )}
        </>
    );
};

export default AppLayout;
