import Cookies from 'js-cookie';
import { CallBack } from '../types';

export const handleLoginGoogle = async (callback: CallBack<boolean>) => {
    const popupWinWidth = 650;
    const popupWinHeight = 650;

    const left = (window.screen.width - popupWinWidth) / 2;
    const top = (window.screen.height - popupWinHeight) / 4;

    const loginWindow = window.open(
        '/api/v1/auth/google',
        'Login',
        'resizable=yes, width=' +
            popupWinWidth +
            ', height=' +
            popupWinHeight +
            ', top=' +
            top +
            ', left=' +
            left
    );

    let interval!: NodeJS.Timer;
    let timeout!: NodeJS.Timer;

    const waitForSuccess = new Promise<boolean>(resolve => {
        interval = setInterval(() => {
            if (Cookies.get('login_success')) {
                Cookies.remove('login_success');
                resolve(true);
            }
        }, 1000);
    });

    const waitTillTimeout = new Promise<boolean>(resolve => {
        timeout = setTimeout(() => resolve(false), 30 * 1000);
    });

    const loginSuccess = await Promise.race([waitForSuccess, waitTillTimeout]);

    interval && clearInterval(interval);
    timeout && clearTimeout(timeout);

    if (!loginWindow?.opener) return;

    loginWindow?.close();

    callback(loginSuccess);
};
