import {
    Blog,
    CreateBlogResponse,
    GetBlogsResponse,
    GetPreSignedBlogImgResponse,
    ResponseData,
} from '../types';
import axiosClient from './axiosClient';

export const createBlogApi = (
    body: Omit<Blog, 'id'>
): Promise<CreateBlogResponse> => axiosClient.post('/blogs', body);

export const getBlogsApi = (): Promise<GetBlogsResponse> =>
    axiosClient.get('/blogs');

export const removeBlogApi = (id: string): Promise<ResponseData> =>
    axiosClient.delete(`/blogs/${id}`);

export const getPreSignedBlogImgApi = (
    fileType: string
): Promise<GetPreSignedBlogImgResponse> =>
    axiosClient.post(`/blogs/upload-img`, { fileType });

export const uploadBlogImgApi = (url: string, file: File): Promise<any> =>
    axiosClient.put(url, file, {
        headers: {
            'Content-Type': file.type,
        },
    });
